import Vue from 'vue'
import Router from 'vue-router'
import CheckoutForm from '@/components/CheckoutForm'
import List from '@/components/List'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/:customer',
      component: CheckoutForm
    },
    {
      path: '/',
      component: List
    }
  ]
})




