// each function accept 3 counts, return the amount of discount.
const pricingRules = {
  'unilever': function (classicCount, standoutCount, premiumCount) {
    let discount = 0
    if (classicCount >= 3) {
      discount = 269.99 * Math.floor(classicCount / 3)
    }
    return discount
  },
  'apple': function (classicCount, standoutCount, premiumCount) {
    let discount = 0
    if (standoutCount > 0) {
      discount = standoutCount * (322.99 - 299.99)
    }
    return discount
  },
  'nike': function (classicCount, standoutCount, premiumCount) {
    let discount = 0
    if (premiumCount >= 4) {
      discount = premiumCount * (394.99 - 379.99)
    }
    return discount
  },
  'ford': function (classicCount, standoutCount, premiumCount) {
    let discount = 0
    if (classicCount >= 5) {
      discount += 269.99 * Math.floor(classicCount / 5)
    }
    if (standoutCount > 0) {
      discount += standoutCount * (322.99 - 299.99)
    }
    if (premiumCount >= 3) {
      discount += premiumCount * (394.99 - 389.99)
    }
    return discount
  }
}

export { pricingRules }




