<template>
  <div class="checkout">
    <h1 class="center">Seek Ads Checkout System</h1>

    <form class="pure-form pure-form-aligned">
      <fieldset>
        <div class="pure-control-group">
          <label for="customer">Customer: </label>
          <span id="customer">{{ customer | capitalize}}</span>
        </div>

        <div class="pure-control-group">
          <label for="sku">Sku: </label>
          <select v-model="curSku">
            <option v-for="skuOption in skuOptions" v-bind:value="skuOption.value">
              {{ skuOption.text }}
            </option>
          </select>
          <button class="pure-button pure-button-primary" v-on:click.prevent="addSku()">Add</button>
        </div>

        <div class="pure-control-group">
          <label for="skus">Sku added: </label>
          <span class="sku-block pure-u-6" v-for="(item, index) in skus" :key="item.id" :item="item">
            {{item}}
            <span class="remove" v-on:click.prevent="deleteSku(index)">x</span>
          </span>
        </div>

        <div class="pure-control-group">
          <label for="total">Total: </label>
          <span id="total">
            {{ total | formatCurrency }}
            <span v-if="discount" style="color: red">({{ discount | formatCurrency }} OFF!)</span>
          </span>
        </div>
      </fieldset>
    </form>
  </div>

</template>

<script>
import { Checkout } from '../js/Checkout.js'
import { pricingRules } from '../js/pricingrules.js'

export default {
  name: 'CheckoutForm',
  data () {
    return {
      curSku: 'classic',
      skuOptions: [
        { text: 'Classic Ads', value: 'classic' },
        { text: 'Standout Ads', value: 'standout' },
        { text: 'Premium Ads', value: 'premium' }
      ],
      skus: [],
      total: 0,
      discount: 0
    }
  },
  methods: {
    addSku: function () {
      this.co.add(this.curSku)
      setTimeout(() => {
        this.total = this.co.total()
        this.discount = this.co.discount
      }, 0)
    },
    deleteSku: function (index) {
      this.co.delete(index)
      setTimeout(() => {
        this.total = this.co.total()
        this.discount = this.co.discount
      }, 0)
    }
  },
  beforeMount () {
    this.customer = this.$route.params.customer || 'default'
    this.co = new Checkout(this.customer, pricingRules)
    this.skus = this.co.products
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .sku-block {
    display: inline-block;
    border: 1px solid grey;
    padding: 5px 15px;
    margin: 5px;
    position: relative;
  }

  .sku-block:hover .remove,
  .sku-block:active .remove {
    visibility: visible;
    cursor: pointer;
  }

  .sku-block .remove {
    visibility: hidden;
  }

</style>



