export function capitalize (value) {
  if (!value) return ''
  value = value.toString()
  return value.charAt(0).toUpperCase() + value.slice(1)
}

export function formatCurrency (value) {
  if (value && typeof value === 'string') value = +value
  if (value === 0) return 0
  return `$${value}`
}



