'use strict'

const CUSTOMERNAME = Symbol('Checkout#CUSTOMERNAME')
const PRICINGRULES = Symbol('Checkout#PRICINGRULES')
const DISCOUNT = Symbol('Checkout#DISCOUNT')

function fix (number, digit = 2) {
  return parseFloat(number.toFixed(digit))
}

export class Checkout {
  constructor (customerName, pricingRules) {
    this[PRICINGRULES] = pricingRules
    this[CUSTOMERNAME] = customerName.toLowerCase()
    this[DISCOUNT] = 0
    this.products = []
  }

  get discount () {
    return this[DISCOUNT]
  }

  add (d) {
    this.products.push(d)
  }

  delete (index) {
    this.products.splice(index, 1)
  }

  total () {
    let classicCount = 0
    let standoutCount = 0
    let premiumCount = 0

    this.products.forEach(d => {
      if (d === 'classic') {
        classicCount++
      } else if (d === 'standout') {
        standoutCount++
      } else if (d === 'premium') {
        premiumCount++
      }
    })

    // console.log(`${this[CUSTOMERNAME]} has ${classicCount} classic Ads, ${standoutCount} standout Ads, ${premiumCount} premium Ads`)

    let total = fix(269.99 * classicCount + 322.99 * standoutCount + 394.99 * premiumCount)

    if (this[PRICINGRULES] &&
      this[PRICINGRULES][this[CUSTOMERNAME]] &&
      typeof this[PRICINGRULES][this[CUSTOMERNAME]] === 'function') {
      this[DISCOUNT] = fix(this[PRICINGRULES][this[CUSTOMERNAME]](classicCount, standoutCount, premiumCount))
      total = fix(total - this[DISCOUNT])
    }

    return total
  }
}



